import React, { Component } from 'react'
import { Button, Image, List, Icon } from 'semantic-ui-react';



export default class StoryList extends Component {
    constructor(props) {
        super(props);

        this.state = {
            stories: this.props.stories
        }
    }
  render() {
    return (
    <List divided verticalAlign='middle'>
    
        { this.state.stories && this.state.stories.map((item, key) => {
            return  <List.Item key={key}>
                <List.Content floated='right'>
                <Button.Group size='medium'>
                <Button icon='thumbs up' circular label={item.points === null ? '0' : item.points} labelPosition='right' />
                </Button.Group>
                </List.Content>
                <List.Content>{item.title}</List.Content>
            </List.Item>
        })}
    </List>
    )
  }
}
